<template>
  <div>
    <div class="shopcar-goods">
      <ul>
        <template v-if="cart.length>0">
          <li v-for="item in cart" :key="item.id">
            <a href="javascript:">
              <div class="goods-left" @click="handleGoodsCheck(item)">
                <div class="goods-left-side goods-left-side-check" v-show="item.checked"></div>
              </div>
              <router-link :to="{}">
                <div class="goods-img">
                  <img :src="item.thumb" alt />
                </div>
                <div class="goods-mid">
                  <h2>BOSC XISTERA</h2>
                  <h3>{{item.name}}</h3>
                  <h4>{{item.sku.name}}</h4>
                  <p>
                    {{item.price | currency}}
                    <span>RMB</span>
                  </p>
                </div>
                <div class="goods-right">
                  <div @click="handleDec(item)">-</div>
                  <input type="text" :value="item.count" />
                  <div @click="handleAdd(item)">+</div>
                </div>
              </router-link>
            </a>
          </li>
        </template>
        <template v-else>
          <div class="empty">购物车是空的</div>
        </template>
      </ul>
    </div>

    <section>
      <goods-list :goods="goods" title="推荐商品"></goods-list>
    </section>

    <!-- 结算 -->
    <div class="account">
      <div class="account-all" @click="handleChooseAll">
        <div style="width:100%;height:100%;background:red;border-radius:50%;" v-show="checkAll"></div>
      </div>
      <span>全选</span>
      <h4>
        合计：
        <b>{{total}}</b>RMB
      </h4>
      <h5>免邮</h5>
      <div class="jsbtn" @click="handleJIE">去结算</div>
    </div>
  </div>
</template>

<script>
import GoodsList from "components/common/goodsList/GoodsList";
import { mapState, mapMutations } from "vuex";
export default {
  name: "CartBody",
  data() {
    return {
      gid: 0,
      page: 1,
      pageSize: 6,
      // 推荐列表
      goods: [],
      checkAll: false,
      // total:0
    };
  },
  components: {
    GoodsList
  },
  computed: {
    ...mapState(["cart"]),
    // eslint-disable-next-line vue/no-dupe-keys
    total() {
      let total = 0;
      this.cart.forEach(v => {
        if (v.checked) {
          total += v.price * v.count;
        }
      });
      return total;
    }
  },
  created() {},
  mounted() {
    this.fetchGoodsData();
  },
  methods: {
    ...mapMutations(["removeFromCart", "countChange"]),
    // 结算
    handleJIE(){
      if(this.total===0){
        this.$toast("您尚未选中任意商品");
        return;
      }
      if(localStorage.getItem("uid")){
        this.$router.push({name:"order"});
      }else{
        this.$router.push({name:"login"});
      }
    },
    handleDec(item) {
      if (item.count === 1) {
        this.removeFromCart(item.id);
      } else {
        item.count--;
        this.countChange();
      }
    },
    handleAdd(item) {
      if (item.count === item.sku.store) {
        this.$toast("商品库存不足");
        return;
      } else {
        item.count++;
        this.countChange();
      }
    },
    handleGoodsCheck(item) {
      if (item.checked) {
        item.checked = false;
      } else {
        item.checked = true;
      }
      this.countChange();
    },
    handleChooseAll() {
      if (this.checkAll == false) {
        this.cart.forEach(v => {
          v.checked = true;
        });
        this.checkAll = true;
      } else {
        this.cart.forEach(v => {
          v.checked = false;
        });
        this.checkAll = false;
      }
      this.countChange();
    },
    // 获取推荐商品
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        state: 1,
        page: this.page,
        pageSize: this.pageSize,
        recommend: 1
      });
      this.goods = res.data.data;
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/shopcar";
.empty {
  width: 100%;
  height: 2.1rem;
  line-height: 2.1rem;
  text-align: center;
}
</style>
